import React from "react"
import styled from "styled-components"
import i18next from "i18next"
const Wrap = styled.div`
  height: 34px;
  display: flex;
  align-items: center;
  background: white;
  border-radius: 17px;
  padding: 0px 1rem;
  border: 1px solid #e9ecee;
  margin-right: 5px;
  // padding-right: 34px;
  & i.fa-search {
    margin-right: 0.5rem;
  }
  & > span {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    & :hover {
      color: #dc3545;
    }
  }
  & input {
    border: 0px;
    outline: none;
    height: 100%;
  }
`
export default function MUIATFilterSearchBar(props) {
  const [textSearch, setTextSearch] = React.useState("")
  return (
    <Wrap className="search-bar">
      <i className="fa fa-search" />
      <input
        type="text"
        placeholder={i18next.t("Search...")}
        value={textSearch}
        onKeyDown={e => {
          if (e.keyCode == 13) {
            // not Pressed ENTER
            console.warn("on enter")
            props.onSearch && props.onSearch(textSearch)
          }
        }}
        onChange={ev => {
          let _v = ev.currentTarget.value
          setTextSearch(_v)
          if (_v == "") {
            props.onSearch && props.onSearch("")
          }
        }}
      />
      {textSearch && textSearch.length > 0 && (
        <span
          onClick={() => {
            setTextSearch("")
            props.onSearch && props.onSearch("")
          }}
        >
          <i className="fa fa-times" />
        </span>
      )}
    </Wrap>
  )
}
